@import url("https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap");

.App {
  color: rgb(224, 156, 192);
  font-family: "Amatic SC";
  text-align: center;
}
h1 {
  margin-block-end: 0em;
}
.main {
  display: flex;
  justify-content: center;
}

.speechBubbleWrapper {
  align-items: center;
  flex-direction: column;
  display: flex;
  position: relative;
}

h2 {
  font-size: 2rem;
  color: black;
  position: absolute;
  width: 200px;

  left: 20%;
}
.grumpy[rotate="1"] {
  animation: grumpy 0.5s linear;
}

@keyframes grumpy {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
  }
}
